.upIconContainer{
    position: fixed;
    bottom: 3rem;
    right: 3rem;
    width: 3rem;
    height: 3rem;
    background-color: #7fffd4;
    color: #333;
    border-radius: 50%;
    filter: drop-shadow('1px');
    box-shadow: 4px 7px 65px -2px rgba(0,0,0,0.52);
    transition: 1s 1s ease;
}